.container {
  margin: auto;
  padding: 10px;
  width: 400px;
  border: 2px dotted rgb(31, 172, 85);
}
.title {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}
.input {
  margin-bottom: 20px;
}
.button__submit {
  margin-left: 20px;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.75);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 250ms linear;
}
.button__submit:hover,
.button__submit:focus {
  background-color: #190be1;
}
